import './App.css';
import React from 'react';
import { useState } from 'react';
import { createContext} from 'react';
import {Punishment} from './LoadJsons';
import {BottomBar} from './BottomBar';

import {createMuiTheme, ThemeProvider} from '@material-ui/core/styles';
import * as colors from '@material-ui/core/colors';
import AppHeader from './TopBar';

export const contextApp = createContext();
export const DEFAULT_CHARACTER = 'マリオ';
export const testmode = false;

const themeRelease = createMuiTheme({
  palette:{
    primary:{
      main: colors.green[800],
    },
    text:{
      primary: '#fff',
    },
    type: 'dark',
  },
});

const themeDebug = createMuiTheme({
  palette:{
    primary:{
      main: colors.yellow[800],
    },
    text:{
      primary: '#fff',
    },
    type: 'dark',
  },
});
function App(){
  const [character, setCharacter] = useState(
    { attacker : DEFAULT_CHARACTER,
      defencer : DEFAULT_CHARACTER,
    });
  const characterState = {state:character, setter:setCharacter};

  console.debug('App');

  return(
    <ThemeProvider theme={(testmode)?themeDebug:themeRelease}>
      <contextApp.Provider value={characterState}>
        <AppHeader />
        <Punishment attacker={characterState.state.attacker} defencer={characterState.state.defencer}/>
        <BottomBar testmode={testmode}/>
      </contextApp.Provider>
    </ThemeProvider>
    );
}

export default App;