import { useState } from 'react';
import { useEffect } from 'react';
import { useReducer } from 'react';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import { makeStyles } from '@material-ui/core/styles';
import {createPunishment} from './createPunishment';

const useStyles = makeStyles((theme) => ({
  table: {
    textAlign: 'center',
  },

  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
  },
}));

const dataFetchReducer = (state, action) => {
    console.log('dataFetchReducer',state , action.type);
    switch (action.type) {
      case 'FETCH_INIT':
        return {
          ...state,
          isLoading: true,
          isError: false
        };
      case 'FETCH_SUCCESS':
        return {
          ...state,
          isLoading: false,
          isError: false,
          data: action.payload,
        };
      case 'FETCH_FAILURE':
        return {
          ...state,
          isLoading: false,
          isError: true,
        };
      default:
        throw new Error();
    }
};
const useDataApis = (initialUrl, initialData) => {
    const [urls, setUrls] = useState(initialUrl);
    const [state, dispatch] = useReducer(dataFetchReducer, {
      isLoading: false,
      isError: false,
      data: initialData,
    });
  
    useEffect(() => {
      const fetchData = async () => {
        console.log('fetchData', urls);
        dispatch({ type: 'FETCH_INIT' });
        try {
            console.log(urls.ShieldsLag);
            console.log(urls.OutOfShieldStartup);
            Promise.all([
                fetch(urls.ShieldsLag),
                fetch(urls.OutOfShieldStartup),
            ])
            .then(([res1, res2]) => Promise.all([res1.json(), res2.json()]))
            .then(function([data1, data2]){
                dispatch({ type: 'FETCH_SUCCESS', payload: {ShieldsLag:data1, OutOfShieldStartup:data2} });
            });
        } catch (error) {
          dispatch({ type: 'FETCH_FAILURE' });
        }
      };
  
      fetchData();
    }, [urls]);
  
    return [state, setUrls];
};
  

function PunishmentList(props){
  console.log('PunishmentList', props);
  const classes = useStyles();
  const rows = createPunishment(props.punishment, props.attacker, props.defencer, props.frames);

  return(
    <div>
      <TableContainer className={classes.container} component={Paper}>
        <Table stickyHeader className={classes.table} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              <TableCell align="center" colSpan="2">ガードさせる:{props.attacker}</TableCell>
              <TableCell align="center" colSpan="3">ガーキャン行動:{props.defencer}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell align="center">硬直差(f)</TableCell>
              <TableCell align="center">ガードさせる技</TableCell>
              <TableCell align="center">行動(前)</TableCell>
              <TableCell align="center">行動(後)</TableCell>
              <TableCell align="center">発生(f)</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              row.OosForward===row.OosBack) ? (
                <TableRow key={row.ShieldsLagframes}>
                  <TableCell component="th" scope="row" align="center">{row.ShieldsLagframes}</TableCell>
                  <TableCell align="center">{row.Move}</TableCell>
                  <TableCell align="center" colSpan="2">{row.OosForward}</TableCell>
                  <TableCell align="center">{row.ShieldsLagframes}</TableCell>
                </TableRow>
                ):(
                <TableRow key={row.ShieldsLagframes}>
                  <TableCell component="th" scope="row" align="center">{row.ShieldsLagframes}</TableCell>
                  <TableCell align="center">{row.Move}</TableCell>
                  <TableCell align="center">{row.OosForward}</TableCell>
                  <TableCell align="center">{row.OosBack}</TableCell>
                  <TableCell align="center">{row.ShieldsLagframes}</TableCell>
                </TableRow>
                )
            )}
            {['0','0',].map((row) =>(
              <TableRow>
                <TableCell component="th"></TableCell>
                <TableCell >{row}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}

function LoadPunishment(props){
    console.log('LoadJson', props);
  
    const [{ data, isLoading, isError }, doFetch] = useDataApis(
      props.loadurls, [],
      );
  
    useEffect(() => {
      doFetch(props.loadurls);
    },[]);// eslint-disable-line react-hooks/exhaustive-deps
  
    return (
      <div>
        {isError && <div>Something went wrong ...</div>}
        {isLoading ? (
          'Loading ...'
        ) : (
          console.log(data),
          <PunishmentList 
            punishment={data} 
            attacker={props.attacker} 
            defencer={props.defencer}
            frames={30}
             />
        )}
      </div>
    );
}

export function Punishment(props){
    console.log('Loadframes', props);
    const loadurls = {ShieldsLag:'json/ShieldsLag.json', OutOfShieldStartup:'json/OutOfShieldStartup.json'};
    return(
      <div>
        <LoadPunishment loadurls={loadurls} attacker={props.attacker} defencer={props.defencer} />
      </div>
    );
}