import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useContext } from 'react';
import {useRef} from 'react';

import AppBar from '@material-ui/core/AppBar';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Grid from '@material-ui/core/Grid';
import {contextApp, DEFAULT_CHARACTER} from './App';

const useStyles = makeStyles((theme) => ({
    appBar:{
      top: 'auto',
      bottom: 0,
    },
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    },
  }));

export function BottomBar(props){
    const classes = useStyles();
    const [attackerlist, setAttackerlist] = useState({});
    const [defencerlist, setDefencerlist] = useState({});
    const [isLoaded, setIsLoaded] = useState(false);
    useEffect(()=>{
      LoadCharacterList(setAttackerlist, setDefencerlist);
    },[]);
  
    const LoadCharacterList = (setShieldslag, setOosstartup) => {
      console.debug('LoadCharactorList');
      fetch('json/CharacterList.json').then(
        function(response){
          return response.json();
        }).then(function(json){
          const rowOosstartup = {};
          const rowShieldslag = {};
    
          for (let key in json) {
            const oosstartup = json[key]['property']['OutOfSheildStartup'];
            if(oosstartup === true){
              rowOosstartup[key] = {'No.':json[key]['No.'], 'Fighter':key};
            }
            const shieldslag = json[key]['property']['ShieldsLag'];
            if(shieldslag === true){
              rowShieldslag[key] = {'No.':json[key]['No.'], 'Fighter':key};
            }
          }
          setOosstartup(rowOosstartup);
          setShieldslag(rowShieldslag);
          setIsLoaded(true);
          console.log(rowOosstartup, rowShieldslag);
        });
      return;
    }
  
    return(
      <React.Fragment>
        <AppBar className={classes.appBar}>
            {(isLoaded) ? (
              (props.testmode) ?(
                <CharacterSetforTest attackerlist={attackerlist} defencerlist={defencerlist}/>
              ):(
                <CharacterSelect attackerlist={attackerlist} defencerlist={defencerlist}/>
              )
            ):(
              <div>Loading...</div>
            )}
        </AppBar>
      </ React.Fragment>
    );
  }
  function CharacterSetforTest(props){
    const character = useContext(contextApp);
    const [counter, setCounter] = useState(0);
    const refCounter = useRef(counter);
    const [startDate, setStartDate] = useState(''); 
    const [finishDate, setFinishDate] = useState('');
    const refAttackerArray = useRef([]);
    const refDefencerArray = useRef([]);
    const refTimerId = useRef();

    useEffect(() =>{
      refCounter.current = counter;
      console.log('counter:', counter);
    },[counter]);

    useEffect(() =>{
        const eventDispatcher = () =>{
            let count = refCounter.current + 1;
            let attackerChecked = false;
            let defencerChecked = false;
            let attacker = DEFAULT_CHARACTER;
            let defencer = DEFAULT_CHARACTER;
            
            if(count < refAttackerArray.current.length){
              attacker = refAttackerArray.current[count];
            }else{
              attackerChecked = true;
            }
            if(count < refDefencerArray.current.length){
              defencer = refDefencerArray.current[count];
            }else{
              defencerChecked = true;
            }

            if(attackerChecked && defencerChecked){
              console.log("finished.");
              setFinishDate(new Date().toLocaleString());
              clearInterval(refTimerId.current);
            }

            setCounter(count);
            character.setter({'attacker':attacker, 'defencer':defencer});
        }
        console.log("start.");
        refAttackerArray.current = Object.keys(props.attackerlist);
        refDefencerArray.current = Object.keys(props.defencerlist);
        setStartDate(new Date().toLocaleString());
        refTimerId.current = setInterval(eventDispatcher, 500);
    },[]);// eslint-disable-line react-hooks/exhaustive-deps
    return(
        <div>
          CharacterSetforTest
            <p>attacker = {refAttackerArray.current[refCounter.current]}</p>
            <p>defencer = {refDefencerArray.current[refCounter.current]}</p>
            <p>counter  = {refCounter.current}</p>
            <p>started  = {startDate}</p>
            <p>finished = {finishDate}</p>
        </div>
    );  

  }

  function CharacterSelect(props){
    return(
      <Grid container alignItems="center" justify="center">
        <Grid item xs={6}>
          <AttackerSelect list={props.attackerlist}/>
        </Grid>
        <Grid item xs={6}>
          <DefencerSelect list={props.defencerlist}/>
        </Grid>
      </Grid>
    );
  }  
  
  function AttackerSelect(props){
    console.debug('AttackerSelect');
    const classes = useStyles();
    const selectedCharacter = useContext(contextApp);
  
    const onChange = (event) => {
      selectedCharacter.setter({attacker:event.target.value, defencer: selectedCharacter.state.defencer});
    }
      return(
      <div>
        <FormControl className={classes.formControl}>
          <InputLabel>ガードさせる</InputLabel>
          <Select defaultValue={DEFAULT_CHARACTER} onChange={onChange}>
            {Object.keys(props.list).map((character) => 
              <MenuItem key={character} value={character}>{character}</MenuItem>
            )}
          </Select>
        </FormControl>
      </div>
    );
  }
  
  function DefencerSelect(props){
    console.debug('DefencerSelect');
    const classes = useStyles();
    const selectedCharacter = useContext(contextApp);
  
    const onChange = (event) => {
      selectedCharacter.setter({attacker: selectedCharacter.state.attacker, defencer:event.target.value});
    }
  
    return(
      <FormControl className={classes.formControl}>
        <InputLabel>ガーキャン行動</InputLabel>
          <Select defaultValue={DEFAULT_CHARACTER} onChange = { onChange }>
            {Object.keys(props.list).map((character) => 
              <MenuItem key={character} value={character}>{character}</MenuItem>
            )}
          </Select>
      </FormControl>
    );
  }
  