function MediaCard(props) {
  const classes = useStyles();
  return (
    <Card className={classes.root}>
      <CardActionArea>
        {("" === props.description.imgurl)?(
            ''
        ):(
            <CardMedia
            className={classes.media}
            image={props.description.imgurl}
          />
        )}
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
              {props.description.title}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
              {props.description.description.split("\n").map((t, i) => {
                    return <div key={i}>{t}</div>
                })}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        {("" === props.description.linkurl)?(
            ""
        ):(
            <Button>
                <Link
                target='_blank'
                rel="noopener noreferrer"
                href={props.description.linkurl}
                >
                    {props.description.linktext}
                </Link>
            </Button>
        )}
      </CardActions>
    </Card>
  );
}
