function createData(OosForward, ShieldsLagframes, Move, OosBack) {
    return {
        'OosForward':OosForward,
        'ShieldsLagframes':ShieldsLagframes,
        'Move': Move,
        'OosBack': OosBack
        };
}

const PropertyList       =    ['DontHave','ToBeUpdated'];
const ConvertProperty    =    {'DontHave':'データなし' , 'ToBeUpdated':'表示未対応'};
const rows=[
    createData('', 0, '',''),
];

const dictProperty ={};
const rowsFrame =[
    createData('', 0, '',''),
];

function _setOosForward(frame, OosForward){
    // if (frame in dictProperty) {
    //     dictProperty[frame].OosForward += OosForward + " "
    // }

    if(frame in rowsFrame){
        rowsFrame[frame].OosForward += OosForward + " ";
    }else{
        let OosForwardString = OosForward + '(' + frame +') ';
        if(frame <= 0 ){
            rowsFrame[0].OosForward += OosForwardString;
        }
        if(frame >= rowsFrame.length){
            rowsFrame[rowsFrame.length-1].OosForward += OosForwardString;
        }
    }
}
function _setOosBack(frame, OosBack){
    // if (frame in dictProperty) {
    //     dictProperty[frame].OosBack += OosBack + " "
    // }
    
    if(frame in rowsFrame){
        rowsFrame[frame].OosBack += OosBack + " ";
    }else{
        let OosBackString = OosBack + '(' + frame +') ';        
        if(frame <= 0 ){
            rowsFrame[0].OosBack += OosBackString;
        }
        if(frame >= rowsFrame.length){
            rowsFrame[rowsFrame.length-1].OosBack += OosBackString;
        }
    }
}
function _setMove(frame, _Move){
    if (frame in dictProperty) {
        dictProperty[frame].Move += _Move + " "
    }
    if(frame in rowsFrame){
        rowsFrame[frame].Move += _Move + " ";
    }else{
        let MoveString = _Move + '(' + frame +') ';
        if(frame <= 0 ){
            rowsFrame[0].Move += MoveString;
        }
        if(frame >= rowsFrame.length){
            rowsFrame[rowsFrame.length-1].Move += MoveString;
        }
    }
}

const initialFramedata = (frames) =>{

    for (let i=0;i<PropertyList.length;i++){
        console.log(PropertyList[i]);
        dictProperty[PropertyList[i]] = {
            'OosForward':'',
            'Move': '',
            'OosBack': '',
            'Property' : PropertyList[i],
            };;
    }
    console.log(dictProperty);

    for(let i=0;i<frames;i++){
        rowsFrame[i] = createData('', i, '','');
    }
}
const setFramedata = (punishment, attacker, defencer, frames) =>{
    initialFramedata(frames);

    if(attacker in punishment.ShieldsLag){
        const attackerFrame = punishment.ShieldsLag[attacker].ShieldsLag;
        console.log('attacker', attacker,attackerFrame);
        Object.keys(attackerFrame).forEach(key=>
            _setMove(attackerFrame[key],key)
            );
    }

    if(defencer in punishment.OutOfShieldStartup){
        const defencerFrame = punishment.OutOfShieldStartup[defencer].OosStartup;
        console.log('defencer', defencer,defencerFrame);
        Object.keys(defencerFrame).forEach(key=>
            {   _setOosBack(defencerFrame[key].back,key);
                _setOosForward(defencerFrame[key].forward,key);
            });
    }

    console.log(dictProperty);
    console.log(rowsFrame);
    for(let i=0;i<rows.length;i++){
        rows[i] = createData('', '', '','');
    }

    rowsFrame[0]['ShieldsLagframes'] += '以下';
    rowsFrame[rowsFrame.length-1]['ShieldsLagframes'] += '以上';
    for(let i=0;i<rowsFrame.length;i++){
        rows[i] = rowsFrame[i];
    }

    let i = rowsFrame.length;
    for(let key of Object.keys(dictProperty)){
        rows[i] = createData(dictProperty[key].OosForward, ConvertProperty[dictProperty[key].Property], dictProperty[key].Move, dictProperty[key].OosBack);
        i++;
    }
    console.log(rows);
    return rows;
}

export const createPunishment = (punishment, attacker, defencer, frames) => {
    console.log('createPunishment', punishment, attacker, defencer, frames); 
    return (('OutOfShieldStartup' in punishment) && ('ShieldsLag' in punishment))?(
            setFramedata(punishment, attacker, defencer, frames)
        ):(
            []
        );
}